.game-review {
  background: white;
  border-radius: 20px;
  padding: 40px;
  max-width: 800px;
  width: 100%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.back-btn {
  padding: 10px 20px;
  background: #f0f0f0;
  border: none;
  border-radius: 8px;
  color: #666;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  margin-bottom: 20px;
}

.back-btn:hover {
  background: #e0e0e0;
  color: #333;
}

.game-review h1 {
  text-align: center;
  color: #333;
  font-size: 2rem;
  margin-bottom: 20px;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: #f8f9ff;
  border-radius: 12px;
  margin-bottom: 20px;
}

.review-info {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.review-category {
  font-weight: 600;
  color: #667eea;
  font-size: 1.1rem;
}

.review-difficulty {
  color: #888;
  text-transform: capitalize;
}

.review-date {
  color: #999;
  font-size: 0.9rem;
}

.review-score {
  font-size: 2rem;
  font-weight: 700;
  color: #667eea;
}

.review-summary {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-bottom: 20px;
}

.summary-stat {
  padding: 20px;
  border-radius: 12px;
  text-align: center;
}

.summary-stat.correct {
  background: linear-gradient(135deg, #d5f4e6 0%, #a8e6cf 100%);
}

.summary-stat.incorrect {
  background: linear-gradient(135deg, #fadbd8 0%, #f5b7b1 100%);
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 5px;
}

.summary-stat.correct .stat-number {
  color: #27ae60;
}

.summary-stat.incorrect .stat-number {
  color: #e74c3c;
}

.stat-label {
  font-size: 1rem;
  font-weight: 600;
  color: #555;
}

.answers-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.answer-item {
  padding: 20px;
  border-radius: 12px;
  border-left: 5px solid;
}

.answer-item.correct {
  background: #f0fff4;
  border-left-color: #27ae60;
}

.answer-item.incorrect {
  background: #fff5f5;
  border-left-color: #e74c3c;
}

.answer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.question-number {
  font-weight: 700;
  font-size: 1.1rem;
  color: #667eea;
}

.result-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
}

.result-badge.correct {
  background: #27ae60;
  color: white;
}

.result-badge.incorrect {
  background: #e74c3c;
  color: white;
}

.question-text {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 15px;
  line-height: 1.5;
}

.answer-details {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.your-answer,
.correct-answer {
  display: flex;
  gap: 8px;
}

.label {
  font-weight: 600;
  color: #666;
}

.correct-text {
  color: #27ae60;
  font-weight: 600;
}

.incorrect-text {
  color: #e74c3c;
  font-weight: 600;
}
