.dashboard {
  background: white;
  border-radius: 20px;
  padding: 40px;
  max-width: 1000px;
  width: 100%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.header-actions {
  display: flex;
  gap: 10px;
}

.settings-btn {
  padding: 10px 20px;
  background: #667eea;
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.settings-btn:hover {
  background: #764ba2;
}

.welcome h1 {
  color: #333;
  font-size: 1.8rem;
  margin-bottom: 5px;
}

.welcome p {
  color: #666;
  font-size: 1rem;
}

.logout-btn {
  padding: 10px 20px;
  background: #f0f0f0;
  border: none;
  border-radius: 8px;
  color: #666;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.logout-btn:hover {
  background: #e0e0e0;
  color: #333;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
  margin-bottom: 30px;
}

.stat-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 15px;
  padding: 20px;
  text-align: center;
  color: white;
}

.stat-number {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.85rem;
  opacity: 0.9;
}

.play-btn {
  width: 100%;
  padding: 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  margin-bottom: 30px;
}

.play-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.4);
}

.dashboard-sections {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.section h2 {
  color: #444;
  font-size: 1.3rem;
  margin-bottom: 15px;
}

.no-games, .no-scores {
  text-align: center;
  color: #999;
  padding: 30px;
  font-style: italic;
  background: #f8f9ff;
  border-radius: 12px;
}

.games-list, .leaderboard-list {
  background: #f8f9ff;
  border-radius: 12px;
  overflow: hidden;
}

.game-entry {
  display: grid;
  grid-template-columns: 1fr auto auto auto;
  gap: 15px;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.game-entry:last-child {
  border-bottom: none;
}

.game-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.game-category {
  font-weight: 600;
  color: #333;
  font-size: 0.95rem;
}

.game-difficulty {
  color: #888;
  font-size: 0.8rem;
  text-transform: capitalize;
}

.game-score {
  font-weight: 700;
  color: #667eea;
  font-size: 1.1rem;
}

.game-date {
  color: #999;
  font-size: 0.85rem;
}

.review-game-btn {
  padding: 8px 16px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s;
}

.review-game-btn:hover {
  background: #764ba2;
  transform: translateY(-2px);
}

.leaderboard-entry {
  display: grid;
  grid-template-columns: 50px 1fr 80px;
  gap: 15px;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.leaderboard-entry:last-child {
  border-bottom: none;
}

.rank-1 { background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%); }
.rank-2 { background: linear-gradient(135deg, #c0c0c0 0%, #e8e8e8 100%); }
.rank-3 { background: linear-gradient(135deg, #cd7f32 0%, #d4a574 100%); }

.rank {
  font-weight: 700;
  color: #667eea;
  font-size: 1.2rem;
}

.name {
  font-weight: 600;
  color: #333;
}

.entry-score {
  font-weight: 700;
  color: #667eea;
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .dashboard-sections {
    grid-template-columns: 1fr;
  }
}

