.results-screen {
  background: white;
  border-radius: 20px;
  padding: 40px;
  max-width: 800px;
  width: 100%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.results-screen h1 {
  text-align: center;
  color: #333;
  font-size: 2.5rem;
  margin-bottom: 30px;
}

.score-display {
  text-align: center;
  margin-bottom: 30px;
}

.final-score {
  font-size: 4rem;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 10px;
}

.performance-message {
  font-size: 1.3rem;
  color: #555;
  margin-top: 10px;
}

.score-saved {
  text-align: center;
  color: #27ae60;
  font-size: 1rem;
  margin-top: 10px;
  font-weight: 600;
}

/* Review Section */
.review-section {
  margin-bottom: 30px;
}

.review-toggle-btn {
  width: 100%;
  padding: 14px;
  background: #f8f9ff;
  border: 2px solid #667eea;
  border-radius: 10px;
  color: #667eea;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.review-toggle-btn:hover {
  background: #667eea;
  color: white;
}

.answers-review {
  margin-top: 20px;
}

.review-summary {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-bottom: 20px;
}

.summary-stat {
  padding: 20px;
  border-radius: 12px;
  text-align: center;
}

.summary-stat.correct {
  background: linear-gradient(135deg, #d5f4e6 0%, #a8e6cf 100%);
}

.summary-stat.incorrect {
  background: linear-gradient(135deg, #fadbd8 0%, #f5b7b1 100%);
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 5px;
}

.summary-stat.correct .stat-number {
  color: #27ae60;
}

.summary-stat.incorrect .stat-number {
  color: #e74c3c;
}

.stat-label {
  font-size: 1rem;
  font-weight: 600;
  color: #555;
}

.answers-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.answer-item {
  padding: 20px;
  border-radius: 12px;
  border-left: 5px solid;
}

.answer-item.correct {
  background: #f0fff4;
  border-left-color: #27ae60;
}

.answer-item.incorrect {
  background: #fff5f5;
  border-left-color: #e74c3c;
}

.answer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.question-number {
  font-weight: 700;
  font-size: 1.1rem;
  color: #667eea;
}

.result-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
}

.result-badge.correct {
  background: #27ae60;
  color: white;
}

.result-badge.incorrect {
  background: #e74c3c;
  color: white;
}

.question-text {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 15px;
  line-height: 1.5;
}

.answer-details {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.your-answer,
.correct-answer {
  display: flex;
  gap: 8px;
}

.label {
  font-weight: 600;
  color: #666;
}

.correct-text {
  color: #27ae60;
  font-weight: 600;
}

.incorrect-text {
  color: #e74c3c;
  font-weight: 600;
}

/* Leaderboard */
.leaderboard {
  margin-bottom: 30px;
}

.leaderboard h2 {
  color: #444;
  font-size: 1.5rem;
  margin-bottom: 20px;
  text-align: center;
}

.no-scores {
  text-align: center;
  color: #999;
  padding: 30px;
  font-style: italic;
}

.leaderboard-list {
  background: #f8f9ff;
  border-radius: 12px;
  overflow: hidden;
}

.leaderboard-entry {
  display: grid;
  grid-template-columns: 50px 1fr 80px;
  gap: 15px;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.leaderboard-entry:last-child {
  border-bottom: none;
}

.rank-1 { background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%); }
.rank-2 { background: linear-gradient(135deg, #c0c0c0 0%, #e8e8e8 100%); }
.rank-3 { background: linear-gradient(135deg, #cd7f32 0%, #d4a574 100%); }

.rank {
  font-weight: 700;
  color: #667eea;
  font-size: 1.2rem;
}

.name {
  font-weight: 600;
  color: #333;
}

.entry-score {
  font-weight: 700;
  color: #667eea;
  font-size: 1.1rem;
}

/* Action Buttons */
.results-actions {
  display: flex;
  gap: 15px;
}

.dashboard-btn {
  flex: 1;
  padding: 16px;
  background: #f0f0f0;
  color: #333;
  border: none;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.dashboard-btn:hover {
  background: #e0e0e0;
  transform: translateY(-2px);
}

.restart-btn {
  flex: 1;
  padding: 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.restart-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.4);
}
